@import url(https://fonts.googleapis.com/css2?family=Poppins&display=swap);
.button{font-size:16px;border-radius:12px;border:none;background-color:#ff6d22 !important;color:white !important;cursor:pointer}.button:hover{box-shadow:0px 0px 1px 3px #ffaf88 !important;border:1px solid #802a00}

.boxed{margin-left:10px}.boxed label{display:inline-block;width:35px;padding:5px;margin-right:10px;border:solid 2px #ccc;transition:all 0.3s;text-align:center;font-family:sans-serif;font-size:14px}.boxed input[type="radio"]{display:none}.boxed input[type="radio"]:checked+label{border:solid 2px #ccc;color:white;background-color:orange}

.boxed{margin-left:10px}.boxed .label{display:inline-block;width:35px;padding:5px;margin-right:10px;border:solid 2px #ccc;transition:all 0.3s;text-align:center;font-family:sans-serif;font-size:14px}.boxed .input[type="radio"]{display:none}.boxed .input[type="radio"]:checked+label{border:solid 2px #ccc;color:white;background-color:orange}

.bottom-nav{box-shadow:-2px -3px 15px 0px rgba(0,0,0,0.11);background-color:#FF6D22}.cta-button{height:50px;padding-top:5px;margin-bottom:-10px}.cta-text{font-size:12px;display:block;color:#23232e;font-weight:bold}

.hero{background-color:#23232e !important;padding-top:20px;margin-top:-20px;padding-bottom:20px}.hero .jumbo-text{text-align:left;margin-top:20px;margin-left:20px}.hero img{max-width:200px;margin-bottom:-25px}

.cta-home{margin:auto}.cta-home .row{margin-bottom:5px;margin:auto}.cta-home .row .card{margin-bottom:5px !important;margin:auto;display:inline-block;background-color:whitesmoke;color:grey;max-width:200px;height:150px;width:140px;border-radius:5px;box-shadow:10px 10px 43px -27px rgba(0,0,0,0.75)}.cta-home .row .card .img-card{padding:10px;background-color:#ffc3a6;border-radius:100%;width:80px;margin:auto}.cta-home .row .card .img-card img{color:#23232e}.cta-home .row .card p{font-family:arial}.cta-home .row .card:hover{background-color:#fc8950;box-shadow:10px 10px 43px -27px #ff6d22;color:black}

.container-sm .img-profile{margin:auto;width:150px;height:150px;object-fit:cover}

.employee{margin-right:10px;margin-top:5px;max-width:400px;width:300px;box-shadow:10px 10px 23px -7px rgba(0,0,0,0.25);border-radius:4px;display:inline-block}.employee .row{padding:10px;padding-bottom:5px;margin-left:5px}.employee .row .image-profile{width:60px;height:50px;display:inline-block}.employee .row .image-profile img{width:50px;height:50px;border-radius:100%;margin-top:5px}.employee .row .col-name{display:inline-block;width:140px;max-width:180px}.employee .row .col-name .employee-name{font-size:13px;font-weight:bold;margin-top:5px;margin-bottom:10px;text-align:left !important}.employee .row .col-name .employee-nik{font-size:13px;font-weight:normal;margin-bottom:-10px;color:grey;text-align:left !important}.employee .row .col-time{display:inline-block}.employee .row .col-time .employee-count{font-size:13px;font-weight:bold;margin-top:5px;margin-bottom:10px;text-align:left !important}.employee .row .col-time .employee-profil{font-size:11px;font-weight:normal;color:white;text-align:center;background-color:#EB6A4D;border-radius:2px;padding:2px}.employee:hover{background:#23232e;color:white}.search{width:300px}.search .icon-search{margin-left:auto;margin-top:-40px;margin-right:20px}

.departemen{margin-right:10px;margin-top:5px;max-width:400px;width:300px;box-shadow:10px 10px 23px -7px rgba(0,0,0,0.25);border-radius:2px;display:inline-block}.departemen .row{margin-bottom:5px}.departemen .row .image-profile{width:60px;height:50px;display:inline-block}.departemen .row .image-profile img{width:100px;height:80px;margin-bottom:-5px}.departemen .row .col-name{display:inline-block;width:190px;max-width:200px;margin-left:50px}.departemen .row .col-name .departemen-name{font-size:16px;font-weight:bold;margin-top:10px;text-align:left !important}.departemen .row .col-name .departemen-supervisor{font-size:13px;font-weight:normal;margin-top:-10px;color:grey;text-align:left !important}.departemen:hover{background:#23232e;color:white}.col-name{color:grey}.col-name .dep-employee-date{font-size:10px}.col-name .dep-employee-name{font-size:15px}


.periode{box-shadow:10px 10px 23px -7px rgba(0,0,0,0.25);border-radius:2px;max-width:400px;margin-top:20px}.periode .col-date{padding-left:20px;padding-top:25px}.periode .period-text-1{font-size:12px;margin-top:-17px}.periode .period-text-2{font-size:14px;font-weight:bold;margin-top:-17px}.periode .col-order{max-width:55px;width:85px;background-color:#98e7d1}.periode .col-order .period-order{font-size:40px;text-align:center;padding-top:25px;color:#1a1a1a}

p,h1,h2,h3,h4,h5,h6,label{font-family:'Poppins', sans-serif !important}body{margin-top:20px}

